/**
 * Subscription model events
 */

'use strict';

import {EventEmitter} from 'events';
import Subscription from './subscription.model';
var SubscriptionEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
SubscriptionEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  Subscription.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    SubscriptionEvents.emit(event + ':' + doc._id, doc);
    SubscriptionEvents.emit(event, doc);
  };
}

export default SubscriptionEvents;
